import TopNavWidget from './widget-top-nav';

require('../core/events/floor');

const $window = $(window);
const { Radio } = window.FN;

export default class TopFloorWidget extends TopNavWidget {
	initAttrs(attrs) {
		attrs = super.initAttrs(attrs);

		return {
			...attrs,
			panelActiveClass: 'panel-activated',
			itemActiveClass: 'active',
			roleSubList: '[data-role="sub-list"]',
			roleSwiper: '[data-role="swiper"]',
			roleToggle: '[data-role="toggle-btn"],[data-role="sub-list"],[data-role="bg"]',
			pageFloor: '[page-floor]'
		};
	}

	initEvents() {
		const events = super.initEvents();

		return {
			...events,
			'click {{attrs.roleToggle}}': 'panelToggle'
		};
	}

	setupRole() {
		const $floor = $(this.get('pageFloor'));
		const $sub = this.$(this.get('roleSubList'));
		const $subItems = $sub.children();
		const $swiper = this.$(this.get('roleSwiper'));
		const $swiperSlides = $swiper.find('.swiper-slide');

		this.role = {
			floor: $floor,
			subList: $sub,
			subListItems: $subItems,
			swiper: $swiper,
			swiperSlides: $swiperSlides
		};
	}

	setup() {
		super.setup();

		this.setupRole();
	}

	renderSwiper() {
		this.swiper = new Swiper(this.role.swiper[0], {
			slidesPerView: 'auto'
		});
	}

	render() {
		super.render();

		this.renderSwiper();

		Radio.on('app:floor:enter', data => {
			this.jumpTo(Math.max(data.index, 0));
		});

		const onClick = (event) => {
			this.switchTo($(event.currentTarget).index());
		};

		this.role.swiperSlides.on('click', onClick);
		this.role.subListItems.on('click', onClick);
	}

	panelToggle() {
		this.el.toggleClass(this.get('panelActiveClass'));
	}

	switchTo(index) {
		const $f = this.role.floor.eq(index);
		if (!$f.length) return;

		this.jumpTo(index);
		$window.scrollTop($f.offset().top + 1);
	}

	jumpTo(index) {
		if (this.activeIndex === index) return;
		this.activeIndex = index;

		const { subListItems, swiperSlides } = this.role;
		const itemActiveClass = this.get('itemActiveClass');

		this.swiper.slideTo(index);
		swiperSlides.eq(index).addClass(itemActiveClass).siblings().removeClass(itemActiveClass);
		subListItems.eq(index).addClass(itemActiveClass).siblings().removeClass(itemActiveClass);
	}
}



