const init = () => {
	FN.Plugin.register({
		swiper: require('common/widget/widget-swiper').default,
		gotop: require('common/widget/widget-gotop').default,
		tab: require('common/widget/widget-tab').default,
		'upgrade-nav': require('./module/fn-upgrade-nav').default,
		'top-floor': require('common/widget/widget-top-floor').default,
		'session-nav':require('./module/fn-session-nav').default,
		'bottom-nav':require('./module/fn-bottom-nav').default,
		LotteryPlugin: require('./module/fn-lottery-widget').default,
		video: require('./module/fn-video-widget').default
	});
};

if (typeof window.define === 'function' && window.define.amd) {
	window.define(['fnModule/common/vendor', 'fnLib/swiper/swiper'], init);
} else {
	init();
}



