export default class Video extends FN.Widget{
	initAttrs(attrs) {
		attrs = super.initAttrs(attrs) || {};
		return {
			roleVideo: '[data-role="video"]',
			...attrs
		};
	}

	render() {
		const $video = this.$(this.get('roleVideo'));
		let isPause = false;

		const pause = () => {
			if (isPause) {
				this.el.removeClass('pause');
				$video[0].play();
			} else {
				$video[0].pause();
				this.el.addClass('pause');
			}

			isPause = !isPause;
		};

		this.el.one('click', () => {
			$video[0].play();
			this.el.removeClass('wifi').addClass('play');

			this.el.on('click', pause);
		});
	}
}



