export default class SessionNav extends FN.Widget{
	render() {
		const $el = this.el;
		const $body = $('body');

		$el.find('[data-role="btn-show"]').on('click', () => {
			$el.addClass('active');
			$body.addClass('ovh');
		});

		$el.find('[data-role="btn-close"],[data-role="mask"]').on('click', () => {
			$el.removeClass('active');
			$body.removeClass('ovh');
		});
	}
}



