export default class BottomNav extends FN.Widget{
	initAttrs(attrs) {
		attrs = super.initAttrs(attrs) || {};
		return {
			selectedClass: 'active',
			...attrs
		};
	}
	render() {
		const $el = this.el;
		const $tabs = this.$('[data-role="tabs"]').children();
		const $panels = this.$('[data-role="panels"]').children();
		const active = this.get('selectedClass');
		const $body = $('body');

		$tabs.on('click', function () {
			const $tab = $(this);

			$tabs.removeClass(active);
			$tab.addClass(active);
			$body.addClass('ovh');

			$el.addClass(active);
			$panels.hide().eq($tab.index()).show();
		});

		this.$('[data-role="btn-close"],[data-role="mask"]').on('click', () => {
			$tabs.removeClass('active');
			$el.removeClass('active');
			$body.removeClass('ovh');
		});
	}
}



