export default class TabWidget extends FN.Widget {
	initAttrs(attrs) {
		attrs = super.initAttrs(attrs);

		return {
			...attrs,
			selectedClass: 'active',
			roleTabs: '[data-role="tabs"]',
			rolePanels: '[data-role="panels"]'
		};
	}

	render() {
		const $tabs = this.$(this.get('roleTabs')).children();
		const $panels = this.$(this.get('rolePanels')).children();
		const active = this.get('selectedClass');

		$tabs.on('click', function () {
			const $tab = $(this);

			$tabs.removeClass(active);
			$tab.addClass(active);

			$panels.hide();
			$panels.eq($tab.index()).show();
		});
	}
}



