require('./load');

const { Radio, Cookie } = window.FN;
const $window = $(window);
const $body = $('body');

const initFloor = ($floorList, pageId) => {
	const info = Cookie.getJSON('pageFloorInfo') || [];

	const index = info.findIndex(item => (item.id === pageId));
	let floorItem;

	// 没有在缓存中找到
	if (index === -1) {
		floorItem = {
			id: pageId
		};
		info.shift();
		info.push(floorItem);

	// 找到则跳转至该楼层
	} else {
		floorItem = info[index];

		const $floor = $floorList.eq(floorItem.level);
		if ($floor.length) {
			$window.scrollTop($floor.offset().top + 1);
		}
	}

	Radio.on('app:window:scroll', data => {
		for (let i = $floorList.length - 1; i >= 0; i--) {
			const $f = $floorList.eq(i);
			/* eslint-disable */
			if (!$f || !$f.length) continue;
			/* eslint-enable */
			const top = $f.offset().top;


			if (data.top >= top) {
				floorItem.level = i;

				Cookie.set('pageFloorInfo', info, { expires: 7 });
				Radio.fire('app:floor:enter', {
					index: floorItem.level,
					floorTop: top,
					windowTop: data.top
				});
				return;
			}
		}

		floorItem.level = null;
		Cookie.set('pageFloorInfo', info, { expires: 7 });
		Radio.fire('app:floor:enter', { index: -1 });
	});
};


Radio.on('app:window:load', () => {
	const $floor = $('[page-floor]');
	const pageId = $body.attr('page-id');
	if (!$floor.length || !pageId) return;

	initFloor($floor, pageId);
});



