require('../../common/core/events/sizechange');
/* 控制刮出百分比
const cmpp = (a,b) => Number(a.replace("%",""))>Number(b.replace("%",""));
*/


const Event = {};
const touch = !!(('ontouchstart' in window) || window.DocumentTouch && document instanceof DocumentTouch);

if (touch) {
	Event.start = 'touchstart';
	Event.move = 'touchmove';
	Event.end = 'touchend';
} else {
	const navigator = window.navigator;
	let desktopEvents = ['mousedown', 'mousemove', 'mouseup'];
	if (navigator.pointerEnabled) desktopEvents = ['pointerdown', 'pointermove', 'pointerup'];
	else if (navigator.msPointerEnabled) desktopEvents = ['MSPointerDown', 'MSPointerMove', 'MSPointerUp'];

	Event.start = desktopEvents[0];
	Event.move = desktopEvents[1];
	Event.end = desktopEvents[2];
}

const pointer = (event) => {
	const result = { x: null, y: null };

	event = event.originalEvent || event || window.event;

	/* eslint-disable */
	event = event.touches && event.touches.length ?
			event.touches[0] : event.changedTouches && event.changedTouches.length ?
				event.changedTouches[0] : event;
	/* eslint-enable */

	if (event.pageX) {
		result.x = event.pageX;
		result.y = event.pageY;
	} else {
		result.x = event.clientX;
		result.y = event.clientY;
	}

	return result;
};

const INSTANCES = [];

const createCanvas = (imgPath, load, error = () => {}) => {
	const canvas = {};
	const node = canvas.node = document.createElement('canvas');
	if (!node.getContext) return error();

	canvas.context = canvas.node.getContext('2d');
	if (!canvas.context) return error();

	const image = new Image();
	image.onload = () => {
		canvas.image = image;
		load(canvas);
	};
	image.onerror = () => {
		error();
	};

	image.src = imgPath;
};


export default class LotteryPlugin extends FN.Widget{
	initAttrs(attrs) {
		attrs = super.initAttrs(attrs) || {};
		return {
			canvasCoverPath: '',
			roleRuleTrigger: '[data-role="rule-trigger"]',
			roleCanvas: '[data-role="canvas"]',
			roleState: '[data-role="state"]',
			roleRuleDialogClose: '[data-role="rule-dialog-close"]',
			showRuleClassName: 'show-rule',
			...attrs
		};
	}

	render() {
		INSTANCES.push(this);
		this.renderRule();
		this.renderCanvas();
	}

	renderRule() {
		const activeClass = this.get('showRuleClassName');
		const $body = $('body');

		this.$(this.get('roleRuleTrigger')).on('click', () => {
			this.el.addClass(activeClass);
			$body.addClass('ovh');
		});

		this.$(this.get('roleRuleDialogClose')).on('click', () => {
			this.el.removeClass(activeClass);
			$body.removeClass('ovh');
		});
	}

	buildCanvas(canvas) {
		const $canvasContainer = this.$(this.get('roleCanvas'));
		$canvasContainer.append(canvas.node);
		this.canvas = canvas;
		this.resize();
		this.isDrawing = false;
		$(canvas.node).on({
			[Event.start]: () => {
				this.isDrawing = true;
			},
			[Event.move]: (e) => {
				if (this.isDrawing) {
					e.preventDefault();
					this.drawPoint(e);
				}
			},
			[Event.end]: () => {
				this.isDrawing = false;
			}
		});
	}

	drawPoint(event) {
		let { x, y } = pointer(event);
		const { context, width, height } = this.canvas;

		const offset = $(this.canvas.node).offset();
		x -= offset.left;
		y -= offset.top;

		context.beginPath();
		context.fillStyle = '#ff0000';
		context.globalCompositeOperation = 'destination-out';
		context.moveTo(x, y);
		context.arc(x, y, 26, 0, Math.PI * 2, false);
		context.fill();

		const imgData = context.getImageData(0, 0, width, height);
		const pixles = imgData.data;
		const transPixs = [];
		for (let i = 0, j = pixles.length; i < j; i += 4) {
			const a = pixles[i + 3];
			if (a < 128) {
				transPixs.push(i);
			}
		}

		const pct = (transPixs.length / (pixles.length / 4) * 100).toFixed(2);

		if (pct > 60) {
			FN.Radio.trigger('drowingComplete');
		}
	}

	resize() {
		if (!this.canvas) return;

		const $canvasContainer = this.$(this.get('roleCanvas'));
		const height = $canvasContainer.height();
		const width = $canvasContainer.width();

		const { node, image, context } = this.canvas;

		node.width = this.canvas.width = width;
		node.height = this.canvas.height = height;

		context.drawImage(image, 0, 0, width, height);
	}

	renderCanvas() {
		createCanvas(this.get('canvasCoverPath'), canvas => {
			this.buildCanvas(canvas);
		});
	}

	switchState(state) {
		const $states = this.$(this.get('roleState')).hide();
		$states.each((_, element) => {
			const $el = $(element);
			if ($el.attr('state') === state) {
				$el.show();
			}
		});

		if (state === 'action') {
			this.isDrawing = false;

			if (this.canvas) {
				$(this.canvas.node).remove();
				this.canvas = null;
			}

			this.renderCanvas();
		}
	}
}


FN.Radio.on('app:window:sizechange', () => {
	for (const p of INSTANCES) {
		p.resize();
	}
});



