export default class SwiperWidget extends FN.Widget {


	render() {


		if (this.$('.swiper-wrapper > *').length <= 1) {
			return;
		}

		const config = this.get('config') || {};

		const pagination = this.$('.swiper-pagination');
		if (pagination.length) {
			config.pagination = pagination;
		}

		this.swiper = new Swiper(this.el[0], config);
	}
}



