require('../core/events/scroll');

const $window = $(window);
const INSTANCES = [];

FN.Radio.on('app:window:scroll', (data) => {
	for (const i of INSTANCES) {
		i[data.top !== 0 ? 'show' : 'hide']();
	}
});

export default class GotopWidget extends FN.Widget {
	setup() {
		super.setup();

		INSTANCES.push(this);
	}

	initEvents() {
		const events = super.initEvents();

		return {
			...events,

			// 事件列表
			click: () => {
				$window.scrollTop(0);
			}
		};
	}

	show() {
		this.el.addClass('show');
	}

	hide() {
		this.el.removeClass('show');
	}
}



