import TopNavWidget from '../../common/widget/widget-top-nav';

export default class UpgradeNav extends TopNavWidget {
	initAttrs(attrs) {
		attrs = super.initAttrs(attrs);

		return {
			...attrs,
			fixedClass: 'fixed',
			slideActiveClass: 'active',
			categoryShowClass: 'show',
			subCategoryExpandedClass: 'expanded',
			collapseHeight: 1.76,
			subPanelBottomPadding: 0.4,
			subPanelItemHeight: 1.68,
			unit: 'rem',

			roleSwiper: '[data-role="swiper"]',
			roleToggle: '[data-role="toggle-btn"]',
			roleSubList: '[data-role="sub-list"]',
			roleSubCategory: '[data-role="sub-category"]',
			roleSubItem: '[data-role="sub-item"]',
			roleSubToggleItem: '[data-role="sub-toggle-item"]',
			roleSubExpand: '[data-role="sub-expand-btn"]',
			roleSubCollapse: '[data-role="sub-collapse-btn"]'
		};
	}

	setup() {
		super.setup();

		this.currentIndex = 0;
	}


	renderSwiper() {
		this.swiper = new Swiper(this.$(this.get('roleSwiper'))[0], {
			slidesPerView: 'auto'
		});
	}

	render() {
		super.render();

		this.renderSwiper();

		const { el } = this;
		const categoryShowClass = this.get('categoryShowClass');
		const slideActiveClass = this.get('slideActiveClass');
		const subCategoryExpandedClass = this.get('subCategoryExpandedClass');
		const $subCategoryList = this.$(this.get('roleSubCategory'));
		const $slides = this.$(this.get('roleSwiper')).find('.swiper-slide');

		$slides.on('click',(event) => {
			const $item = $(event.currentTarget);
			const slideIndex = $item.index();

			$slides.removeClass(slideActiveClass);
			$item.addClass(slideActiveClass);

			if (slideIndex > $slides.length - 1) {
				return;
			}

			//this.switchTo(slideIndex);
			this.currentIndex = slideIndex;
		});

		$subCategoryList.each((index, category) => {
			if (index > $slides.length - 1) {
				return;
			}

			const $category = $(category);

			$category.find(this.get('roleSubExpand')).on('click', () => {
				$category.addClass(subCategoryExpandedClass);
				this.switchTo(this.currentIndex);
			});

			$category.find(this.get('roleSubCollapse')).on('click', () => {
				$category.removeClass(subCategoryExpandedClass);
				this.switchTo(this.currentIndex);
			});
		});

		this.$(this.get('roleToggle')).on('click', () => {
			if (el.hasClass(categoryShowClass)) {
				this.switchTo(-1);
			} else {
				this.switchTo(this.currentIndex);
			}
		});
	}

	switchTo(index) {
		const { el } = this;
		const categoryShowClass = this.get('categoryShowClass');

		const collapseHeight = this.get('collapseHeight');
		const unit = this.get('unit');

		if (index === -1) {
			el.removeClass(categoryShowClass).css('min-height', `${collapseHeight}${unit}`);
			return;
		}

		const $subCategoryList = this.$(this.get('roleSubCategory'));
		const $subCategory = $subCategoryList.eq(index);

		el.addClass(categoryShowClass);
		/*$subCategoryList.hide();
		$subCategory.show();*/

		let size = $subCategory.find(this.get('roleSubItem')).length;

		if ($subCategory.hasClass(this.get('subCategoryExpandedClass'))) {
			size += $subCategory.find(this.get('roleSubToggleItem')).length;
		}

		const subPanelBottomPadding = this.get('subPanelBottomPadding');
		const subPanelItemHeight = this.get('subPanelItemHeight');

		const minHeight = Math.round(size / 4) * subPanelItemHeight + collapseHeight + subPanelBottomPadding;
		el.css('min-height', `${minHeight}${unit}`);
	}

	toggleFixed(fixed) {
		super.toggleFixed(fixed);

		if (fixed) {
			if (!this.isFixed) {
				this.switchTo(-1);
			}
		}

		this.isFixed = fixed;
	}

}



