require('../core/events/scroll');

const INSTANCES = [];

FN.Radio.on('app:window:scroll', (data) => {
	for (const i of INSTANCES) {
		i.toggleFixed(data.top >= i.el.offset().top);
	}
});

export default class TopNavWidget extends FN.Widget {
	setup() {
		super.setup();

		INSTANCES.push(this);
	}

	initAttrs(attrs) {
		attrs = super.initAttrs(attrs);

		return {
			...attrs,
			fixedClass: 'fixed'
		};
	}

	toggleFixed(flag) {
		this.el[flag ? 'addClass' : 'removeClass'](this.get('fixedClass'));
	}
}



